<script>
  import { onMount } from "svelte";
  import {
    nicknames,
    fetchNicknames,
    nickname,
    mode,
    refresh_access_token
  } from "./store.js";

  let nicknameexceptme = [];
  let myScore = 0;
  let winner;
  let winnerScore = 5;

  onMount(async () => {
    fetchNicknames($mode);
    nicknameexceptme = $nicknames.filter((item) => item !== $nickname);
  });

  function validateInput(value, options) {
    if (!options.includes(value)) {
      return null;
    }
    return value;
  }

  async function submitrecord() {
    const data = {
      winner: winner,
      winnerScore: winnerScore,
      myScore: myScore,
    };
    let checkjwt = localStorage.getItem("d2rpvpjwtToken");

    if (!checkjwt) {
      alert("다시 로그인해주세요");
      return;
    }

    if (myScore > 4 || myScore < 0) {
      alert("점수 입력이 잘못되었습니다");
      return;
    }

    const sendSubmitRequest = async () => {
      const response = await fetch("/submitrecord", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${checkjwt}`,
        },
        body: JSON.stringify(data),
      });

      return response;
    };

    try {
      let response = await sendSubmitRequest();

      if (response.ok) {
        alert("기록 전송 완료");
      } else if (response.status === 403) {
        await refresh_access_token();
        checkjwt = localStorage.getItem("d2rpvpjwtToken");

        response = await sendSubmitRequest();

        if (response.ok) {
          alert("기록 전송 완료");
        } else {
          alert(`오류 발생: ${response.status}`);
        }
      } else {
        alert(`오류 발생: ${response.status}`);
      }
    } catch (error) {
      console.error("오류 발생:", error);
      alert("오류 발생: " + error.message);
    }
  }

  function handle_winner_change(event) {
    winner = validateInput(event.target.value, nicknameexceptme);
  }
</script>

<div class="left">
  승자 고르기
  <input
    list="nicknames_list"
    bind:value={winner}
    on:blur={handle_winner_change}
    class="namewidth"
    placeholder="승자를 선택하세요"
  />
  <datalist id="nicknames_list">
    {#each nicknameexceptme as option}
      <option value={option}>{option}</option>
    {/each}
  </datalist>

  내 점수 입력
  <input type="number" class="scorewidth" bind:value={myScore} min="0" max="4" />
  <button on:click={submitrecord}>보내기</button>
</div>
